.button{
  display: block;
    width: 500px;
    background-color: darkblue;
    color: white;
    font-size: 23px;
    font-weight: bolder;
    border: none;
    padding: 15px;
    margin: 40px auto 0 auto;
  }
  
  .button:hover{
      cursor: pointer;
      transform: scale(1.005)
  }

  .button:disabled{
    cursor: not-allowed;
    background-color: #888;
  }

  @media (max-width: 500px) {
    .button{
      width: 95%;
    }
  }
  